
@import "mixin/mixins";
@import "mixin/normal";

html,
body {
	font-size: 12px;
	font-family: 'Open Sans', sans-serif; }

.inline-block {
	vertical-align: middle;
	display: inline-block; }

.container-fluid {
	max-width: 1170px!important;
	padding: 0px 15px;
	font-size: 0;
	position: relative; }

.navbar-collapse {
    padding-right: 0;
    padding-left: 0; }
a:focus {
	outline: none; }

header {
	.head {
		border-bottom: 1px solid $border_grey; }
	.menu {
		width: 85.2%;
		ul {
			text-align: justify;
			font-size: 0;
			width: 100%;
			padding: 0 25px;
			transition: 0.2s;
			&:after {
				content: "";
				width: 100%;
				display: inline-block; }
			li {
				font-size: 12px;
				float: none;
				display: inline-block;
				vertical-align: middle;
				text-align: center; } }


		.nav {}
		border-left: 1px solid $border_grey;
		padding: 0 5px;
		a {
			padding: 0;
			margin: 10px 0;
			color: #757575;
			font-weight: 400;
			&:hover {
				color: #673ab7;
				background: none; } } } }

.language_dropdown {
	width: 14.8%;
	min-width: 68px;
	font-size: 12px;
	display: inline-block;
	vertical-align: top;
	text-align: center;
	height: 40px;
	border-right: 1px solid $border_grey;
	border-left: 1px solid $border_grey;
	position: relative;
	&:hover {
		box-shadow: 0 6px 12px rgba(0,0,0,.175);
		&:after {
			content: "";
			display: block;
			width: 100%;
			height: 5px;
			background: #fff;
			position: absolute;
			left: 0;
			bottom: -1px;
			z-index: 1000; } } }

.language_dropdown a {
	display: block;
	padding: 10px 13.3333%; }

.language_dropdown a,
.language_dropdown a:hover,
.language_dropdown a:active,
.language_dropdown a:focus {
	text-decoration: none;
	color: #555;
	font-weight: 400;
	background: none; }

.active:hover {
	background: none; }

.us-flag {
	padding-left: 8px; }

.language_dropdown {
	.arrow-down {
		padding: 0 6.0606%; }
	.dropdown-menu {
		width: 300px;
		padding: 20px;
		position: absolute;
		top: 100%;
		right: -1px;
		left: auto;
		margin-top: 0;
		border-radius: 0px;
		border: 1px solid $border_grey;
		.box_for_btn {
			margin-top: 10px;
			text-align: right;
			button.save {
				color: white;
				font-family: OpenSans;
				font-size: 12px;
				font-weight: 400;
				width: 100px;
				height: 30px;
				background-color: $yellow;
				border-radius: 3px;
				display: inline-block;
				&:hover {
					background-color: $yellow-hover; } } } } }

select[name="language"],
select[name="currency"] {
	margin-top: 10px;
	padding: 9px 15px;
	width: 100%;
	border: 1px solid $border_grey;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	&:focus {
		outline: none; } }

.select {
	position: relative;
	display: inline-block;
	width: 100%; }
.select::after {
	content: '\f107';
	font-family: "FontAwesome";
	font-style: normal;
	font-weight: normal;
	position: absolute;
	top: 20px;   right: 15px;
	pointer-events: none; }

.header-body {
	padding-top: 10px;
	.logo {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 10px;
		width: 70px;
		height: 78px;
		margin: 0 4.2105%;
		margin-top: 10px;
		transition: 0.5s;
		img {
			-o-object-fit: cover;
			object-fit: cover;
			height: 100%;
			width: 100%; } }

	form.search {
		display: inline-block;
		vertical-align: top;
		width: 42.0175%;
		margin-left: 27px;
		position: relative;
		transition: 0.4s;
		margin-top: 27px;
		input[type="search"] {
			color: #757575;
			font-family: OpenSans;
			font-size: 12px;
			font-weight: 400;
			height: 45px;
			width: 100%;
			background-color: #f8f8f8;
			border: 1px solid $border_grey;
			border-radius: 5px;
			padding: 17px; }

		.search_button {
			display: inline-block;
			vertical-align: middle;
			max-width: 150px;
			width: 35%;
			height: 45px;
			background-color: $yellow;
			box-shadow: 0 1px 2px rgba(0, 0, 0, 0.16);
			border-radius: 45px;
			position: absolute;
			top: 0;
			right: 0;
			transform: translateX(50%);
			color: white;
			font-size: 16px;
			font-weight: 600;
			&:hover {
				background: #ffd453; } } } }

.header_box_for_buttons {
	margin-left: 8.6%;
	vertical-align: top;
	font-size: 0;
	text-align: right;
	display: inline-block;
	vertical-align: middle;
	.header_big_button {
		display: inline-block;
		vertical-align: middle;
		min-width: 51px;
		text-align: center;
		cursor: pointer;
		padding: 36px 29px 31px;
		position: relative;
		transition: 0.3s;
		.button_img {
			height: 27px;
			margin: 0 auto;
			margin-bottom: 40px;
			img {
				-o-object-fit: cover;
				object-fit: cover; } }
		span {
			color: #555;
			font-size: 14px;
			font-weight: 400;
			i {
				font-style: normal; } }
		.cart {
			padding-left: 16%;
			padding-right: 16%; } }
	.header_big_button:hover,
	.header_big_button.active_big_button {
		box-shadow: 0 0 10px rgba(163, 161, 161, 0.35);
		span {
			color: #673ab7; }
		a {
			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 7px;
				background: #fff;
				position: absolute;
				left: 0;
				bottom: 0px;
				z-index: 1000; } } } }
.wishlist_img {
	width: 28px; }
.cart_img {
	width: 24px; }
.account_img {
	width: 28px; }
.header_big_button:last-child {
	padding: 0; }
.account_dropdown {
	display: inline-block;
	vertical-align: middle;
	a {
		display: block;
		padding: 36px 29px 31px;
		transition: 0.3s; }
	.dropdown-menu {
		cursor: auto;
		width: 300px;
		position: absolute;
		background-color: white;
		box-shadow: 0 0 10px rgba(163, 161, 161, 0.35);
		right: 0;
		left: auto;
		top: 100%;
		.top_block {
			padding: 20px 20px 0; }
		.social_block {
			font-size: 0;
			text-align: justify;
			&:after {
				content: '';
				display: inline-block;
				width: 100%;
				height: 0px;
				visibility: hidden;
				overflow: hidden; } }
		p {
			color: #555;
			font-size: 12px;
			font-weight: 400;
			padding: 0;
			margin: 0; }
		button.social {
			display: inline-block;
			color: #212121;
			font-size: 14px;
			font-weight: 600;
			padding: 0;
			.fa {
				margin: 5px;
				vertical-align: middle; } }
		.sign_button {
			width: 260px;
			height: 30px;
			background-color: $yellow;
			color: #fff;
			border-radius: 3px;
			font-size: 12px;
			font-weight: 400;
			margin-top: 10px;
			margin-bottom: 10px;
			&:hover {
				background-color: #ffd453; } } }

	ul.account_menu {
		margin: 0;
		padding: 0;
		list-style-type: none;
		li {
			border-bottom: 1px solid #f1f1f1;
			&:last-child {
				border-bottom: none; } }
		a {
			padding: 10px 20px;
			color: #555;
			font-size: 14px;
			font-weight: 400;
			&:hover {
				background: #dcdcdc; } } } }

.select_category {
	margin-top: -50px;
	margin-left: 12px;
	position: static;
	display: inline-block;
	vertical-align: top;
	.select_category_stripes {
		width: 16px;
		.stripe {
			width: 100%;
			height: 2px;
			margin-bottom: 3px;
			opacity: 0.5;
			background-color: #757575; }
		.triangle {
			position: relative;
			width: 100%;

			&::after {
				content: '';
				height: 0px;
				width: 0px;
				border: 3px solid transparent;
				border-top: 6px solid #757575;
				position: absolute;
				top: 0;
				right: -10px; } }
		.last {
			margin-bottom: 0; } }
	.select_category_title {
		font-style: normal;
		text-transform: uppercase;
		font-size: 12px;
		font-weight: 600;
		color: #212121;
		margin-left: 18px;
		margin-right: 10px;
		vertical-align: middle; }
	.see_all {
		font-style: italic;
		color: #212121;
		font-size: 10px;
		font-weight: 400;
		text-decoration: underline;
		vertical-align: middle; }
	.dropdown-menu {
		left: 50%;
		transform: translate(-50%,0);
		top: 100%;
		z-index: 100; }
	.categories_block {
		transition: 0.3s;
		width: 119.3%;
		padding: 0px 9.6491%;
		margin: 0 auto;
		text-align: center;
		background-color: whitesmoke;
		box-shadow: 0 0 12px rgba(0, 0, 0, 0.16);
		.category_line {
			margin: 10px;
			background: #fff;
			font-size: 0;
			a.category_line_item {
				margin: 28px 0px;
				width: 228px;
				display: inline-block;
				cursor: pointer;

				.category_item_image {
					display: inline-block;
					vertical-align: middle;
					max-height: 41px;
					margin: 0 18px;
					img {
						object-fit: contain; } }
				.category_item_title {
					display: inline-block;
					vertical-align: middle;
					width: 60.5263%;
					color: #6b6b6b;
					font-size: 16px;
					font-weight: 600;
					&:hover {
						color: #734ea4; } } } } } }

main {
	background: #f5f5f5;

	.jumbo {
		.container-fluid {
			height: 100%; }
		.slider-jumbo,
		.static-block {
			display: inline-block;
			vertical-align: top;
 }			// height: 100%
		.slider-jumbo {
			width: 68.4211%; }

		.static-block {
			width: 31.5789%; }
		img {
			object-fit: cover;
			height: 100%;
			width: 100%; } }

	.slick-dots {
		bottom: 0;
		right: 0;
		text-align: right;
		padding-right: 30px;
		padding-bottom: 8px;
		li {
			button {
				background: #fff;
				border-radius: 50%;

				&::before {
					content: ''; } } }
		li.slick-active {
			button {
				background: #ffc107; } } }
	.slick-dotted.slick-slider {
		margin-bottom: 0; }

	h2.categories_title {
		color: black;
		font-size: 24px;
		font-weight: 700;
		text-transform: uppercase;
		margin: 27px 0;
		margin-left: 32px; }

	.see_all_btn {
		color: #212121;
		font-size: 14px;
		font-weight: 600;
		font-style: italic;
		border-bottom: 1px solid #212121;
		vertical-align: top;
		margin-left: 45px; }

	.categories {
		padding: 20px 0;
		.carusel {
			padding: 30px 55px;
			background: #fff; }
		.carusel_item {
			display: inline-block;
			vertical-align: top;
			width: 150px;
			margin: 0 25px; }
		.carusel_item_image {
			display: block;
			margin: 17px auto;
			width: 100%;
			max-width: 66px; }

		.carusel_item_title {
			color: #757575;
			font-size: 16px;
			font-weight: 400;
			text-align: center;
			margin-bottom: 23px;
			max-width: 150px;
			&:hover {
				color: $purple; } }
		.slick-prev {
			left: 55px;
			&::before {
				content: '\f104';
				font-family: "FontAwesome";
				color: #bdbdbd;
				font-size: 36px; }
			&:hover {
				&::before {
					color: $purple; } } }
		.slick-list {
			margin: 0 22px; }
		.slick-next {
			right: 55px;
			font-size: 0;
			&:before {
				content: '\f105';
				color: #bdbdbd;
				font-size: 36px;
				font-family: "FontAwesome"; }
			&:hover {
				&::before {
					color: $purple; } } } } }

//products net
.item {
	margin:  0 0 50px 0;
	a {
		display: block; }
	&:hover {
		color: inherit; } }
.col_content {
	height: 400px;
	background: #fff;
	padding: 20px 10px;
	// cursor: pointer
	display: block;
	&:hover {
		box-shadow: 0.5px 0.9px 19px rgba(201, 199, 199, 0.6);
		text-decoration: none; } }
.top_evaluate {
	position: relative; }

.item_rating {
	font-size: 14px;
	color: #ffc107;
	margin-left: 10px;
	margin-bottom: 19px; }
.like_ikon {
	font-size: 14px;
	color: #bbbbbb;
	position: absolute;
	right: 10px;
	top: 0;
	font-size: 15px;
	&:hover {
		color: red; } }
.product_picture {
	height: 206px;
	border-radius: 5px;
	img {
		height: 100%;
		width: 100%;
		object-fit: cover; } }
.category_name {
	height: 12px;
	color: #888;
	font-size: 12px;
	font-weight: 400;
	text-align: center;
	margin: 20px 0; }
.product_name {
	height: 13px;
	color: #222;
	font-size: 15px;
	font-weight: 600;
	text-align: center;
	margin-bottom: 25px; }
.price_holder {
	height: 26px;
	text-align: center;
	&>* {
		display: inline-block;
		vertical-align: middle; } }
.product_price_title {
	height: 16px;
	color: #222;
	font-size: 18px;
	font-weight: 600;
	line-height: 16px;
	text-align: center;
	margin: 5px 20px; }
.old_price_title {
	height: 16px;
	color: #222;
	font-size: 12px;
	font-weight: 400;
	position: relative;
	&::after {
		content: '';
		display: block;
		position: absolute;
		height: 1px;
		width: 100%;
		background: #e93214;
		transform: rotate(-15deg);
		transform-origin: left bottom;
		bottom: 2px;
		left: 0; }

	.discount {
		height: 26px;
		font-size: 14px;
		font-weight: 600;
		line-height: 12px;
		color: #222;
		padding: 7px 14px;
		background-color: #f65d44;
		box-shadow: 0.5px 0.9px 6px rgba(204, 56, 31, 0.3);
		border-radius: 13px; }

	.item:hover {
		.col_content {
			background: #fed457;
			box-shadow: 0.5px 0.9px 19px rgba(226, 177, 30, 0.6);
			.super_deal_rating,
			.like_ikon,
			.category_name,
			.product_name {
				color: #fff; }
			.product_price_title {
				color: #e93214; } } }
	.blue_highlight {
		.col_content {
			background: #86d1f0;
			box-shadow: 0.5px 0.9px 19px rgba(86, 185, 226, 0.6);
			.super_deal_rating,
			.like_ikon,
			category_name,
			.product_name,
			.product_price_title,
			.old_price_title,
			.discount {
				color: #fff; } } } }

.discounts_block {
	h2 {
		margin-top: 0; }
	.discount-slide {
		position: relative;
		.slide-image {
			height: 330px;
			background: url("../img/Layer-1.png");
			background-size: cover;
			margin-bottom: 30px; } }
	.slide-text-in-center {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%; }
	.slide-title {
		color: white;
		font-family: 'Playfair Display', serif;
		font-size: 30px;
		font-weight: 700;
		text-transform: uppercase;
		text-align: center;
		margin: 0 auto; }
	button.explore {
		width: 158px;
		height: 40px;
		background-color: #ffc107;
		border-radius: 20px;
		box-shadow: 0 1px 2px rgba(0, 0, 0, 0.16);
		color: white;
		font-size: 14px;
		font-weight: 700;
		text-align: center;
		padding: 15px 0;
		text-transform: uppercase;
		line-height: 14px;
		margin: 0 auto;
		margin-top: 30px;
		display: block;
		&:hover {
			background: #ffd453; } }
	.slick-dots {
		text-align: center;
		padding-right: 0;
		bottom: -30px;
		li {
			button {
				height: 9px;
				width: 9px; } } } }


footer {
	padding: 50px 0;
	.logo_footer {
		display: inline-block;
		vertical-align: top;
		width: 24.3860%;
		text-align: center; }

	h2 {
		color: #757575;
		font-weight: 400;
		font-size: 14px;
		margin-top: 0;
		margin-bottom: 20px; }
	.menu_holder {
		display: inline-block;
		vertical-align: top;
		width: 75.6140%; }

	.menu_footer,
	.menu_footer ul {
		list-style-type: none;
		margin: 0;
		padding: 0;
		color: #757575;
		font-weight: 400;
		font-size: 12px; }
	.menu_footer {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: 22.6218%;
		min-width: 135px;
		padding: 0 15px;
		a {
			color: #757575;
			&:hover {
				color: darken(#757575, 50%); } } }
	.copyright {
		margin: 0;
		margin-left: 95px;
		color: #757575;
		font-size: 11px;
		font-weight: 400; } }

















